<template>
  <div class="tab-control">
    <div
      v-for="(item, index) in titles"
      :key="item"
      class="tab-control-item"
      :class="isActive(index)"
      @click="itemClick(index)"
    >
      <span>{{ item }}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    titles: {
      type: Array,
      default() {
        return []
      },
    },
  },
  data() {
    return {
      currentIndex: 0,
    }
  },
  methods: {
    isActive(index) {
      return { active: this.currentIndex === index }
    },
    itemClick(index) {
      this.currentIndex = index
      this.$emit('tabClick', index)
    },
  },
}
</script>

<style scoped>
.tab-control {
  display: flex;
  width: 100%;
  font-size: 15px;
  background-color: #fff;
}

.tab-control-item {
  flex: 1;
  line-height: 40px;
  text-align: center;
}

.tab-control-item span {
  padding: 5px;
}

.active {
  color: var(--color-high-text);
}

.active span {
  border-bottom: 3px solid var(--color-tint);
  font-weight: 600;
}
</style>



